.collapse-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ff6060;
  margin: 0 auto;
  margin-bottom: 10px;
  max-width: 900px;
  color: #f6f6f6;
  border-radius: 10px;
  padding: 10px 20px;
}

button {
  width: 30px;
  height: 30px;
  background: transparent;
  border: 0;
}

.vector {
  height: 30px;
  width: 30px;
  rotate: 180deg;
  cursor: pointer;
}

.title {
  position: absolute;
}

h1 {
  color: white;
  -webkit-margin-after: 10px;
          margin-block-end: 10px;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  font-size: 20px;
}

.texte {
  height: -webkit-max-content;
  height: max-content;
  background-color: #f6f6f6;
  font-size: 1.3em;
  padding: 10px 20px;
  position: relative;
  top: -20px;
  max-width: 900px;
  margin: 0 auto;
}/*# sourceMappingURL=collapse.css.map */
.cover {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.imgAccueil {
  width: 100%;
  height: 180px;
  -webkit-filter: contrast(0.5);
          filter: contrast(0.5);
  border-radius: 15px;
}

.cover p {
  font-family: "montserrat";
  position: absolute;
  color: white;
  font-size: 34px;
  -webkit-transform: translate(10px, 50px);
          transform: translate(10px, 50px);
  z-index: 6;
}

.listCard {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #F6F6F6;
  margin: 0 30px;
  border-radius: 15px;
}

.card {
  display: flex;
  flex-direction: column-reverse;
  height: 240px;
  width: 280px;
  margin: 10px 20px;
  border-radius: 15px;
  background: rgb(255, 96, 96);
  background: linear-gradient(180deg, rgb(255, 96, 96) 35%, rgba(13, 9, 12, 0.5788690476) 100%);
}

.card img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  object-fit: cover;
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.card b {
  position: absolute;
  margin: 10px;
  color: white;
  font-size: 18px;
  width: 20%;
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 699px) {
  body {
    margin: 0 20px;
  }
  .imgAccueil {
    height: 100px;
  }
  .listCard {
    background-color: white;
    margin: 0 0;
  }
  .card {
    height: 100%;
    width: 100%;
    margin: 10px 0px;
  }
  .card b {
    width: 40%;
  }
  .cover {
    justify-content: flex-start;
  }
  .cover p {
    font-size: 25px;
    width: 50%;
  }
}/*# sourceMappingURL=Home.css.map */
* {
  font-family: "montserrat";
  color: #FF6060;
}

img {
  width: 100%;
  height: 100%;
}

.content {
  display: flex;
  justify-content: space-between;
  margin: 20px;
}

.content p {
  font-size: 18px;
  color: #FF6060;
}

.content b {
  color: #FF6060;
  font-size: 36px;
  background-color: white;
}

.content.img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #C4C4C4;
}

.bloc-right {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
}

.host {
  display: flex;
}

.host img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
}

.host p {
  width: 80px;
  color: #FF6060;
  text-align: -webkit-right;
  margin-right: 10px;
}

.tags {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.tags li {
  justify-content: flex-start;
  color: white;
  background-color: #FF6060;
  border-radius: 10px;
  width: 115px;
  padding: 5px 5px;
  margin: 5px 5px;
  text-align: center;
}

.description {
  display: flex;
  margin: 10px;
}

.description div {
  margin: 0 5px;
  flex: 1 1;
}

.text div, .equipements ul {
  background-color: #F6F6F6;
}

.texte li {
  list-style: none;
}

.equipements, .text {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 20px;
}

.active-star {
  fill: #FF6060;
}

h2 {
  padding: 15px 20px;
  background-color: #FF6060;
  border-radius: 10px;
  color: white;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

svg {
  width: 30px;
  height: 30px;
  fill: #C4C4C4;
}

.equipements ul {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 500px) {
  .host img {
    width: 32px;
    height: 32px;
  }
}
@media screen and (max-width: 699px) {
  body {
    margin: 0 20px;
  }
  .host p {
    width: 50px;
  }
  .content {
    margin: 0;
    flex-direction: column;
  }
  .content b {
    font-size: 18px;
  }
  .content p {
    font-size: 14px;
  }
  .description div {
    margin: 5px 0px;
  }
  .bloc-right {
    flex-direction: row;
    justify-content: space-between;
  }
  .tags ul {
    justify-content: flex-start;
    width: -webkit-max-content;
    width: max-content;
    flex-wrap: wrap;
  }
  .tags li {
    width: 115px;
  }
  .NbRating {
    display: flex;
    align-items: center;
  }
  .description {
    flex-direction: column;
  }
  .equipements, .text {
    width: 100%;
    margin: 20px 0;
  }
  svg {
    width: 15px;
    height: 15px;
    overflow: visible;
    margin: 0 8px;
  }
  h2 {
    font-size: 13px;
    padding: 5px 10px;
  }
}/*# sourceMappingURL=Logement.css.map */
.slider {
  display: flex;
  flex-direction: column;
  align-content: center;
  position: relative;
}

.slider img {
  border-radius: 25px;
}

.vectorNext {
  height: 30px;
  width: 30px;
  position: absolute;
  rotate: 90deg;
  cursor: pointer;
  top: 50%;
  right: 0;
  padding: 0 0;
}

.vectorPrev {
  height: 30px;
  width: 30px;
  position: absolute;
  rotate: -90deg;
  cursor: pointer;
  top: 50%;
  padding: 0 0;
}

.image-number {
  font-size: 15px;
  color: white;
  position: absolute;
  left: 50%;
  bottom: 20px;
}/*# sourceMappingURL=caroussel.css.map */
.bodyError {
  background-color: #E5E5E5;
}

.blocError {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blocError p {
  font-size: 36px;
  padding: 0 20px;
}

strong {
  font-size: 288px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #FF6060;
}

.bodyError p {
  font-size: 36px;
}

.blocError a {
  padding: 70px;
  font-size: 18px;
  text-decoration: underline;
}

@media screen and (max-width: 829px) {
  strong {
    font-size: 96px;
  }
  .blocError p {
    font-size: 18px;
    text-align: center;
  }
  .blocError a {
    font-size: 14px;
  }
}/*# sourceMappingURL=Error.css.map */
